        .cajon.color-primario.p-4.mb-4(style="background-color: #FFF7E5")
          p.mb-0 El desarrollo web se refiere a la construccin, creacin y mantenimiento de sitios web. Incluye aspectos como el diseo web, la publicacin web, la programacin web y la base de datos gestin. Techlib.
    .d-flex.flex-wrap.mb-5
