      .col-lg-6.ps-lg-4(data-aos="fade-left")
        p La mayora de las grandes empresas tursticas tienen un equipo de desarrollo web dedicado a disear y mantener su sitio web; sin embargo, en los ltimos aos organizaciones ms pequeas, emprendedores e incluso personas naturales del sector estn utilizando sistemas de gestin de contenido como WordPress, Drupal y Joomla, para crear y editar sus propios sitios web utilizando un sitio web interfaz.
        p En este sentido surge la inquietud sobre cmo iniciar a desarrollar una pgina web Pues bien, se invita a ver el siguiente video, en el que encontrar algunos <em>tips</em> importantes:

    .tarjeta.color-primario.p-3.col-xl-10.mx-auto.mb-5
