      h3.py-2.px-4(style="background-color: #B6E1FD; border-radius: 50px") Creacin de la pgina web

    p.mb-5(data-aos="fade-right") Para la creacin de la pgina web de la empresa turstica es importante definir el plan de trabajo y evaluar las diferentes opciones disponibles para poder elegir la ms conveniente. Revise los aspectos ms importantes que se deben tener en cuenta:

    .tarjeta.p-4.mb-5(style="background-color: #EAEAEA")
      PasosB.color-secundario(data-aos="fade-left")
        .px-4.pt-lg-5.row(titulo="Paso 1")
          .col-lg-6.mb-4.mb-lg-0
            h3 Definir la idea de la pgina
            p La empresa debe definir cul es el propsito principal y la funcionalidad de la pgina, estos son algunos de los tipos de sitios ms utilizados para la comercializacin de experiencias tursticas:

          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema1/img-11.svg' style="max-width: 525px" alt='Imagen decorativa')
        
        .px-4.pt-lg-5.row(titulo="Paso 2")
          .col-lg-6.mb-4.mb-lg-0
            h3 Registrar el nombre del dominio
            p Antes de crear la pgina web de la empresa turstica es necesario pensar en un nombre atractivo.

          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema1/img-12.svg' style="max-width: 324px" alt='Imagen decorativa')
        
        .px-4.pt-lg-5.row(titulo="Paso 3")
          .col-lg-6.mb-4.mb-lg-0
            h3 Elegir un servidor (<em>hosting</em>) web
            p La empresa debe elegir un proveedor de servidor (<em>hosting</em> que se ajuste a las necesidades de la pgina web de comercializacin de experiencias tursticas).

          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema1/img-13.svg' style="max-width: 505px" alt='Imagen decorativa')
        
        .px-4.pt-lg-5.row(titulo="Paso 4")
          .col-lg-6.mb-4.mb-lg-0
            h3 Elegir la plataforma para crear la pgina
            p La eleccin de la plataforma para crear la pgina web depende del objetivo que tenga la empresa turstica. Estas son algunas de las opciones disponibles.

          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema1/img-14.svg' style="max-width: 563px" alt='Imagen decorativa')

        .px-4.pt-lg-5.row(titulo="Paso 5")
          .col-lg-6.mb-4.mb-lg-0
            h3 Publicar, monitorear y mejorar 
            p La empresa turstica debe tener informacin sobre el rendimiento de su pgina web, para esto es necesario recopilar estadsticas que le permitan analizar y tomar decisiones para mejorar continuamente. 
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema1/img-15.svg' style="max-width: 505px" alt='Imagen decorativa')
    
    p.mb-5(data-aos="fade-right") Dentro de los sistemas de gestin de contenidos ms conocidos est el Wordpress, a continuacin, encontrar el paso a paso para la creacin de una pgina web:

