    .d-flex.mb-5(data-aos="flip-up")
      h3.py-2.px-4(style="background-color: #B6E1FD; border-radius: 50px") Estructura de la pgina web
    
    p.mb-5(data-aos="fade-right") Una vez la empresa turstica decide montar una pgina web es importante que conozca cules son los elementos bsicos de una estructura web.  Se invita a ver en el siguiente recurso los elementos que hacen parte de la estructura de la pgina web (Marketing, 2017):

    ImagenInfografica.color-acento-botones.mb-5.col-xl-8.col-lg-10.mx-auto(data-aos="fade-left")
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/temas/tema1/img-16.svg' alt='Imagen que muestra puntos de estructura de una pgina web, los cuales se describen en la parte inferior.')

      .tarjeta.p-3(x="63.2%" y="20%" numero="+" style="background-color: #FFF7E3")
        .h5.mb-2 Cabecera <em style="font-weight: normal;">(Header)</em>
        p Esta es la seccin que se encuentra al inicio de la pgina, por este motivo debe ser atractiva. Contiene el logotipo o nombre de la pgina web y una imagen principal (<em>banner</em>) o un <em>slider</em> con varias fotografas que pasan automticamente.

      .tarjeta.p-3(x="31.2%" y="42.3%" numero="+" style="background-color: #FFF7E3")
        .h5.mb-2 Contenido <em style="font-weight: normal;">(Body)</em>
        p El contenido es todo el texto, imgenes, productos y/o servicios, etc. que se encuentra entre la cabecera y el pie de pgina. Toda esta informacin es la que va hacer que Google comprenda qu servicios ofrece la web de la empresa turstica y la posicione en su buscador.  
      
      .tarjeta.p-3(x="9.3%" y="19.2%" numero="+" style="background-color: #FFF7E3")
        .h5.mb-2 Men
        p Es una barra que se encuentra en la parte superior de la web (en la cabecera), en donde aparecen los principales apartados que tiene la pgina de la empresa turstica (quienes somos, servicios, contacto, productos y/o servicios y blogs). Este apartado suele estar anclado en la parte superior de la web y no vara en ninguna de las secciones internas para que el usuario pueda verlo en todo momento. 
      
      .tarjeta.p-3(x="69.3%" y="57.2%" numero="+" style="background-color: #FFF7E3")
        .h5.mb-2 Barras laterales <em style="font-weight: normal;">(Sidebar)</em>
        p Las barras laterales se utilizan para mostrar informacin importante que debe estar a la vista del usuario, como por ejemplo, un formulario de registro, las ltimas ofertas o una imagen que enlace a alguna de las experiencias tursticas que se venda en la web, entre otras. 
      
      .tarjeta.p-3(x="48.2%" y="90.3%" numero="+" style="background-color: #FFF7E3")
        .h5.mb-2 Pie de pgina <em style="font-weight: normal;">(Footer)</em>
        p El pie de pgina es la parte inferior de la pgina web, en esta zona se aprovecha para poner secciones de la web que tengan menos inters para los usuarios (aviso legal, poltica de <em>cookies</em>, etc.), datos de contacto de la empresa turstica y las redes sociales. 

    .d-flex.mb-5(data-aos="flip-up")
      h3.py-2.px-4(style="background-color: #B6E1FD; border-radius: 50px") Diseo adaptable (<em>responsive</em>)
    
    .d-flex.flex-wrap.mb-5
      img.col-lg-6.mb-4.mb-lg-auto(src='@/assets/curso/temas/tema1/img-17.jpg' alt='Imagen decorativa' data-aos="fade-right")
      .col-lg-6.ps-lg-4(data-aos="fade-left")
        p El diseo adaptable o <em>responsive</em> es utilizado en los sitios web para adaptar los contenidos de diversos formatos a los dispositivos que utilice el usuario (computadores, tabletas, telfonos inteligentes), esto genera un mayor alcance de la marca y mejora la experiencia de los usuarios al visitar el sitio web de la empresa turstica.
        p Cada da ms personas utilizan sus telfonos mviles para acceder a Internet, en ocasiones reemplazando a los PC. Es por este motivo que la empresa debe tener un sitio web adaptable (<em>responsive</em>) para comercializar sus experiencias tursticas, esto le genera ms clientes potenciales y una ventaja sobre las empresas del sector que no lo tienen. Entre muchas bondades que tiene el desarrollo web con diseo adaptable se reconocen las siguientes:

