            p Busca facilitar el acceso universal a Internet. Es por esto que en el momento de disear y programar el sitio web la empresa turstica debe adaptarlo sin importar las limitaciones fsicas, intelectuales o sensoriales de los usuarios.
            p.mb-0 La WAI (Web Accessibility Initiative) desde 1999 emite directrices estandarizadas y reconocidas a nivel internacional para crear contenidos y pginas ms accesibles que se agrupan en los siguientes aspectos:
          img.mb-4.mb-lg-auto.mx-auto.order-1.order-lg-2(src="@/assets/curso/temas/tema1/img-33.png" style="max-width: 336px; margin-top: -5%" alt="Imagen decorativa" data-aos="fade-left")
    

