          .crd_hover_txt--body
            h4.mb-3.text-center(style="min-height: 43px") Accesibilidad del contenido web (WCAG)
            p.mb-0 Pautas para crear contenidos accesibles y compatibles con todo tipo de tecnologas de apoyo, dispositivos, navegadores y lenguajes de programacin. Contemplan el uso de letras de gran tamao, diseos adaptativos, textos predictivos, asistentes a la navegacin, etc.

      .col-xl-4.col-md-6.col-11.mb-4.mb-xl-0(data-aos="flip-up")
