          .crd_hover_txt--body
            h4.mb-3.text-center(style="min-height: 43px") Accesibilidad de las herramientas de autor (ATAG)
            p.mb-0 Normas que ataen al desarrollo de los programas y aplicaciones que se utilizan para crear, gestionar y publicar los contenidos digitales. Estas herramientas abarcan los procesadores de textos, los gestores de bases de datos, los programas de edicin de vdeo, etc.

